@font-face {
  font-family: "General Sans";
  src: url("../../assets/fonts/GeneralSans-Variable.woff2") format("woff2"), url("../../assets/fonts/GeneralSans-Variable.woff") format("woff");
  font-weight: 100 900; /* Variable range */
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "General Sans";
  src: url("../../assets/fonts/GeneralSans-VariableItalic.woff2") format("woff2"), url("../../assets/fonts/GeneralSans-VariableItalic.woff") format("woff");
  font-weight: 100 900; /* Variable range */
